import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import { withLocalization } from "../../shared/localization";
import getSharedStyles from '../../../style';
import Checkbox from "@material-ui/core/Checkbox";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import CheckBoxOutlineBlankIcon from "@material-ui/icons/CheckBoxOutlineBlank";
import CheckBoxIcon from "@material-ui/icons/CheckBox";
import classNames from "classnames";

const styles = (theme) => ({
    ...getSharedStyles(theme),
  checkbox: {
    color: `${theme.palette.secondary.main}!important`,
    padding: 0,
  },
  checkboxLight: {
    color: "white!important",
  },
  checkboxIcon: {
    fontSize: "24px",
  },
  controlNoLabel: {
    marginRight: "0",
  },
  formControlLabelRoot: {
    marginLeft: '0',
    marginRight: '0',
  },
  label: {
      marginLeft: '5px',
  }
});

function CheckboxFieldComponent(props) {
  const { classes } = props;
  const label = props.label || props.translate("field." + props.field);
  const noLabel = props.noLabel ? props.noLabel : false;
  return (
    <FormControlLabel
      control={
        <Checkbox
          //value={props.value}
          checked={props.value}
          onChange={(e) => props.onChange(props.field, e.target.checked)}
          classes={{ root: classNames(classes.checkbox, props.light && classes.checkboxLight) }}
          icon={
            <CheckBoxOutlineBlankIcon
              className={classNames(classes.checkboxIcon)}
            />
          }
          checkedIcon={
            <CheckBoxIcon className={classNames(classes.checkboxIcon)} />
          }
          disabled={props.disabled}
        />
      }
      label={!noLabel && label}
      classes={{ root: props.noLabel && classes.controlNoLabel, root: classes.formControlLabelRoot, label: classNames(classes.label, props.labelClasses) }}
      className={classNames(props.className)}
    />
  );
}

CheckboxFieldComponent.propTypes = {
  onChange: PropTypes.func.isRequired,
  field: PropTypes.string.isRequired,
  label: PropTypes.string,
  value: PropTypes.string,
  noLabel: PropTypes.bool,
  disabled: PropTypes.bool,
  labelClasses: PropTypes.string,
  checkboxLight: PropTypes.bool,
};

export default withLocalization(withStyles(styles)(CheckboxFieldComponent));
